angular.module('app').run(['$templateCache', function($templateCache) {$templateCache.put('app/components/Footer.html','<footer class="footer">\n  <span class="todo-count">\n    <strong>{{$ctrl.activeCount || \'No\'}}</strong> {{$ctrl.activeCount === 1 ? \'item\' : \'items\'}} left\n  </span>\n  <ul class="filters">\n    <li key="filter" ng-repeat="filter in $ctrl.filters">\n      <a ng-class="{\'selected\': filter === $ctrl.selectedFilter.type}" ng-click="$ctrl.handleChange(filter)">{{$ctrl.filterTitles[filter]}}</a>\n    </li>\n  </ul>\n  <button ng-if="$ctrl.completedCount > 0" class="clear-completed" ng-click="$ctrl.handleClear()">\n    Clear completed\n  </button>\n</footer>');
$templateCache.put('app/components/Header.html','<header class="header">\n  <h1>todos</h1>\n  <todo-text-input new-todo="true" on-save="$ctrl.handleSave(text)" placeholder="What needs to be done?"></todo-text-input>\n</header>');
$templateCache.put('app/components/MainSection.html','<section class="main">\n  <input ng-if="$ctrl.todos.length" class="toggle-all" type="checkbox" ng-checked="$ctrl.todos.reduce($ctrl.completeReducer, 0) === $ctrl.todos.length" ng-click="$ctrl.handleCompleteAll()">\n  <ul class="todo-list">\n    <todo-item ng-repeat="todo in $ctrl.todos.filter($ctrl.selectedFilter.filter)" todo="todo" on-destroy="$ctrl.handleDestroy(id)" on-change="$ctrl.handleChange(id)" on-save="$ctrl.handleSave(todo)"></todo-item>\n  </ul>\n  <footer-component ng-if="$ctrl.todos.length" completed-count="$ctrl.todos.reduce($ctrl.completeReducer, 0)" active-count="$ctrl.todos.length - $ctr.todos.reduce($ctrl.completeReducer, 0)" filter="$ctrl.selectedFilter" on-clear-completed="$ctrl.handleClearCompleted()" on-show="$ctrl.handleShow(filter)"></footer-component>\n</section>');
$templateCache.put('app/components/TodoItem.html','<li ng-class="{\'editing\': $ctrl.editing, \'completed\': $ctrl.todo.completed}">\n  <todo-text-input ng-if="$ctrl.editing" text="$ctrl.todo.text" editing="$ctrl.editing" on-save="$ctrl.handleSave(text)"></todo-text-input>\n  <div class="view" ng-if="!$ctrl.editing">\n    <input class="toggle" type="checkbox" ng-checked="$ctrl.todo.completed" ng-click="$ctrl.onChange({id: $ctrl.todo.id})">\n    <label ng-dblclick="$ctrl.handleDoubleClick()">{{$ctrl.todo.text}}</label>\n    <button class="destroy" ng-click="$ctrl.handleDestroy($ctrl.todo.id)"></button>\n  </div>\n</li>');
$templateCache.put('app/components/TodoTextInput.html','<input class="textInput" ng-class="{\'edit\': $ctrl.editing, \'new-todo\': $ctrl.newTodo}" ng-model="$ctrl.text" ng-keypress="$ctrl.handleSubmit($event)" ng-blur="$ctrl.handleBlur()" placeholder="{{$ctrl.placeholder}}" type="text">');
$templateCache.put('app/containers/App.html','<header-component todos="$ctrl.todos"></header-component>\n<main-section todos="$ctrl.todos" filter="$ctrl.filter"></main-section>\n');}]);